//
// Vertical Navbar alignment options
//
// Display the navbar fixed to the top or bottom of the page.

// Fix the left/right navbars when screen real estate supports it
.navbar-fixed-left,
.navbar-fixed-right {
  margin-bottom: 0; // override .navbar defaults
  width: @navbar-width;

  position: fixed;
  top: 0;
  bottom: 0;
  z-index: @zindex-navbar-fixed;
  .translate3d(0, 0, 0);

  // Undo the rounded corners
  @media (min-width: @grid-float-breakpoint) {
    border-radius: 0;
  }
}
.navbar-fixed-left {
  left: 0;
  border-width: 0 0 0 1px;
}
.navbar-fixed-right {
  right: 0;
  border-width: 0 1px 0 0;
}


// Brand/project name

.navbar-brand {

  // Override .navbar defaults
  .navbar-fixed-left &,
  .navbar-fixed-right & {
    float: none;
    display: inline-block;

    // padding: @navbar-padding-vertical @navbar-padding-horizontal;
    // font-size: @font-size-large;
    // line-height: @line-height-computed;
    // height: @navbar-height;

    // &:hover,
    // &:focus {
    //   text-decoration: none;
    // }

  }

  @media (min-width: @grid-float-breakpoint) {
    .navbar-fixed-left > .container &,
    .navbar-fixed-right > .container &,
    .navbar-fixed-left > .container-fluid &,
    .navbar-fixed-right > .container-fluid & {
      margin-left: -@navbar-padding-horizontal;
    }
  }
}




